
@import "variable.less";
* {box-sizing: border-box; -webkit-tap-highlight-color: rgba(0,0,0,0);}
/* 禁用iPhone中Safari的字号自动调整 */
html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    /* 解决IOS默认滑动很卡的情况 */
    -webkit-overflow-scrolling : touch;
    font-size: 62.5%;
}
body{ background: #F4F5F7; }
body,
dl, dt, dd, ul, ol, li,
h1, h2, h3, h4, h5, h6,
pre, code, form, fieldset, legend, input, textarea,
p, blockquote, th, td, hr, button,
article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  margin: 0;
  padding: 0;
  font-size: 1.6rem;
  color: #141414;
}
ul, ol {list-style: none;}
table {border-collapse: collapse; border-spacing: 0;}
input, select, button, textarea {font-size: 100%; font: inherit; -webkit-appearance: none; /*在iOS上，清除输入框默认有内部阴影*/}

/* 取消链接高亮 (点击时候黑色阴影) */
body, div, ul, li, ol, h1, h2, h3, h4, h5, h6, input, textarea, select, p, dl, dt, dd, a, img, button, form, table, th, tr, td, tbody, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-size: 1.6rem;
    color: @baseColor;
}
/* 隐藏滚动条 */
body::-webkit-scrollbar {
    display: none;
    width: 0;
    color: transparent;
    height: 0;
}
/* 按钮文本不可选 */
button {user-select: none;}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
    color: #999;  /*输入框提示语的字体样式*/
}
button,input[type='button'],
input[type='reset'],
input[type='submit'] {
    -webkit-appearance: button;  /*渲染成button的风格*/
    text-transform: none;
    outline: none;
}
input::-webkit-inner-spin-button {/*解决input的type="number"在部分手机端会出现一个小按钮*/
    -webkit-appearance: none;/*去除系统默认appearance的样式,常用于IOS下移除原生样式*/
}

/* 默认不要下划线 */
a {text-decoration: none;background: transparent; -webkit-tap-highlight-color: transparent; color: #000; }
/* 移动端点击a链接出现蓝色背景问题解决 */
a:link,a:active,a:visited,a:hover {
    background: none;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    -webkit-tap-highlight-color: transparent;
}

/* 加载不出来的图片不要显示灰色边框 */
img:not([src]),img[src=""] {opacity: 0;}
// img {vertical-align: middle; border:none;max-width: 100% !important; }
/* 设置HTML5元素为块 */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
    display: block;
}

/* 清除浮动 */

.cdct_clearfix::before,
.cdct_clearfix::after {
    display: table;
    content: " ";
}

.cdct_clearfix::after {
    clear: both;
}

.cdct_clearfix {
    *zoom: 1;
}


/*单行溢出*/
.cdct_onetxtcut{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
/*多行溢出 手机端使用*/
.cdct_txtcut{
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.weui-toast{
    width: 12rem !important;
}


