//tab
.cdct_tab_wrapper{
    display: flex;
    box-shadow:0px 1px 3px 0px rgba(0,0,0,0.06);
    height:50px;
    background: #fff;
    padding: 5px 0;
    margin-top:56px;
    border-top: 1px solid #E1E1E1;
    .cdct_tab_li{
        flex: 1;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: #333333;
        position: relative;
        border-right: 1px solid RGBA(0, 0, 0, 0.06);
        &:last-child{
            border-right: 0;
        }
        img{
            margin-left: 10px;
        }
    }
}
.vux-tab{
    height: 50px !important;
    .vux-tab-item{
        font-size: 1.6rem !important;
        color: #333333 !important;
        line-height: 50px !important;
        &.vux-tab-selected{
            color:#3CB963 !important; 
        }
    }
}
.vux-tab-wrap,.vux-tab-container{
    height: 50px !important;
}

.cdct_container{
    .vux-tab-item{
        position: relative;
        &:before{
            position: absolute;
            content: '';
            width: 1px;
            height: 34px;
            background: #E1E1E1;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
        }
    }
    .vux-tab-item:nth-of-type(3){
        &:before{
            width:0;
        }
    }
    .vux-tab-wrap{ 
        padding-top: 50px; 
        .vux-tab-container{
            height: 50px;
            .vux-tab{
                height: 50px;
                .vux-tab-item{
                    line-height: 50px;
                }
            }
        }
    }
    .vux-slider {
        height: calc(~'100% - 50px');
        overflow: hidden;
        .vux-swiper{
            overflow-y: scroll;
        }
    }
    
}

.error{
