.vripple-wrapper{
    position: relative;
    overflow: hidden;
    
}
.vripple-wrapper .vripple-circle{
    display:inline-block;
    position : absolute;
    pointer-events:none;
    top: 0;
    left: 0;
    background: rgba(255, 215, 215,0);
    overflow: hidden;
    z-index: 999;
}



